.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: whitesmoke;
  padding: 0 40px;

  .left {
    max-width: calc(50vw - 60px);
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 50px;
  }

  .right {
    max-width: calc(50vw - 60px);
    width: 100%;
  }
}
